import axios from 'axios';
import * as vscode from 'vscode';
import { ConfigManager } from '../config/configManager';
import { Logger } from '../utils/logger';
import { BaseClient, ModelConfig, ModelProvider } from './baseClient';

/**
 * Ollama API
 * Ollama
 */
export class OllamaClient implements BaseClient {
    private logger: Logger;
    private modelConfig: ModelConfig;

    constructor(
        private configManager: ConfigManager,
        modelConfig?: ModelConfig
    ) {
        this.logger = Logger.getInstance();
        this.modelConfig = modelConfig || {
            title: configManager.getModelName(),
            model: configManager.getModelName(),
            provider: ModelProvider.OLLAMA,
            apiBase: configManager.getApiUrl(),
            temperature: configManager.getTemperature(),
            maxTokens: configManager.getMaxTokens()
        };
    }

    /**
     * 
     * @param context 
     * @returns 
     */
    public async getCompletion(context: any): Promise<string | null> {
        try {
            const apiUrl = this.modelConfig.apiBase || this.configManager.getApiUrl();
            const modelName = this.modelConfig.model || this.configManager.getModelName();
            const temperature = this.modelConfig.temperature || this.configManager.getTemperature();
            const maxTokens = this.modelConfig.maxTokens || this.configManager.getMaxTokens();
            
            this.logger.debug(`: ${modelName}, : ${temperature}, : ${maxTokens}`);
            
            // 
            const prompt = this.buildPrompt(context);
            
            // 
            if (this.configManager.isDebugEnabled()) {
                this.logger.debug(`:\n${prompt}`);
            } else {
                // 100
                this.logger.debug(`100: ${prompt.substring(0, 100)}...`);
            }
            
            // 
            const requestData = {
                model: modelName,
                prompt: prompt,
                temperature: temperature,
                max_tokens: maxTokens,
                options: {
                    num_predict: maxTokens
                }
            };
            
            // 
            this.logger.debug(` Ollama API: ${apiUrl}/api/generate`);
            this.logger.debug(`: ${JSON.stringify({
                model: modelName,
                temperature: temperature,
                max_tokens: maxTokens,
                prompt_length: prompt.length
            })}`);
            
            // 
            this.logger.debug(`fetch...`);
            this.logger.debug(`: ${JSON.stringify(requestData).length} `);
            
            const response = await fetch(`${apiUrl}/api/generate`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(requestData)
            });
            
            // 
            this.logger.debug(`Ollama API : ${response.status} ${response.statusText}`);
            const headers: Record<string, string> = {};
            response.headers.forEach((value, key) => {
                headers[key] = value;
            });
            this.logger.debug(`: ${JSON.stringify(headers)}`);
            
            // 
            if (!response.ok) {
                const errorText = await response.text();
                this.logger.error(`API: ${response.status} ${response.statusText} - ${errorText}`);
                return null;
            }
            
            // 
            const responseText = await response.text();
            
            // 
            //this.logger.debug(`API:\n${responseText}`);
            //this.logger.debug(`: ${responseText.length} `);
            //this.logger.debug(`100: ${responseText}`);
            
            // JSON
            const openBraces = (responseText.match(/\{/g) || []).length;
            const closeBraces = (responseText.match(/\}/g) || []).length;
            this.logger.debug(`JSON: {  ${openBraces} , }  ${closeBraces} `);
            
            // response
            const hasResponse = responseText.includes('"response"');
            this.logger.debug(`response: ${hasResponse}`);
            
            // JSON
            let completionText = '';
            
            // response
            if (responseText.includes('"response"')) {
                try {
                    // 
                    const lines = responseText.split('\n').filter(line => line.trim() !== '');
                    
                    // response
                    for (const line of lines) {
                        try {
                            const jsonObj = JSON.parse(line);
                            if (jsonObj && jsonObj.response) {
                                completionText += jsonObj.response;
                            }
                        } catch (parseError) {
                            this.logger.debug(`: ${parseError.message}, : ${line.substring(0, 50)}...`);
                        }
                    }
                    
                    this.logger.debug(`, : ${completionText.length}`);
                    
                    // 
                    if (completionText.trim() === '```' || completionText.trim() === '``' || completionText.trim().length <= 3) {
                        this.logger.debug(``);
                        completionText = '';
                    }
                } catch (error) {
                    this.logger.error(`: ${error.message}`);
                    completionText = '';
                }
            }
            
            // 
            if (!completionText || completionText.trim().length <= 5) {
                this.logger.debug(``);
                try {
                    // response
                    let allResponses = '';
                    const regex = /"response":[ ]*"([^"]*)"/g;
                    let match;
                    
                    while ((match = regex.exec(responseText)) !== null) {
                        if (match[1]) {
                            // 
                            const responseValue = match[1].replace(/\\n/g, '\n').replace(/\\r/g, '\r').replace(/\\"/g, '"');
                            allResponses += responseValue;
                        }
                    }
                    
                    if (allResponses.length > 0) {
                        this.logger.debug(`: ${allResponses.length}`);
                        completionText = allResponses;
                    }
                } catch (error) {
                    this.logger.debug(`: ${error.message}`);
                }
            }
            
            // 
            if (!completionText || completionText.trim().length === 0) {
                // JSON
                this.logger.debug('');
                const directExtract = this.extractCompletionDirectly(responseText);
                if (directExtract) {
                    completionText = directExtract;
                    this.logger.debug(`: ${completionText.length}`);
                } else {
                    // cleanJsonResponse
                    try {
                        const cleanedJson = this.cleanJsonResponse(responseText);
                        const jsonObj = JSON.parse(cleanedJson);
                        if (jsonObj.response) {
                            completionText = jsonObj.response;
                            this.logger.debug(`JSONresponse: ${completionText.length}`);
                        } else {
                            // response
                            completionText = this.extractAnyContent(responseText);
                            this.logger.debug(`: ${completionText ? completionText.length : 0}`);
                        }
                    } catch (error) {
                        this.logger.error(`JSON: ${error.message}`);
                        // 
                        completionText = this.extractAnyContent(responseText);
                        this.logger.debug(`: ${completionText ? completionText.length : 0}`);
                    }
                }
            }
            
            // 
            this.logger.debug(``);
            
            // Markdown
            if (completionText.startsWith('```')) {
                const firstLineBreak = completionText.indexOf('\n');
                if (firstLineBreak !== -1) {
                    // ```python```
                    const codeBlockHeader = completionText.substring(0, firstLineBreak);
                    this.logger.debug(`: "${codeBlockHeader}"`);
                    completionText = completionText.substring(firstLineBreak + 1);
                } else {
                    this.logger.debug(`: "${completionText}"`);
                    completionText = '';
                }
                
                // ```
                const lastCodeBlockEnd = completionText.lastIndexOf('```');
                if (lastCodeBlockEnd !== -1) {
                    completionText = completionText.substring(0, lastCodeBlockEnd).trim();
                    this.logger.debug(`"\`\`\`"`);
                }
            }
            
            // 
            const processedCompletion = this.postProcessCompletion(completionText, context);
            
            // 
            if (processedCompletion) {
                this.logger.debug(`: ${processedCompletion.length}`);
                this.logger.debug(`100: ${processedCompletion.substring(0, 100)}${processedCompletion.length > 100 ? '...' : ''}`);
            } else {
                this.logger.debug(``);
            }
            
            return processedCompletion;
        } catch (error) {
            this.logger.error(`: ${error.message}`, error);
            return null;
        }
    }

    /**
     * JSON
     */
    private cleanJsonResponse(text: string): string {
        this.logger.debug(`JSON: ${text.length}`);
        
        // JSON
        if (!text || text.trim() === '') {
            return '{"response": ""}';
        }
        
        // BOM
        let cleaned = text.replace(/^\uFEFF/, '');
        
        // JSON
        cleaned = cleaned.trim();
        
        // OllamaJSON
        const lines = cleaned.split('\n');
        if (lines.length > 1) {
            this.logger.debug(`: ${lines.length}`);
            
            //  - JSON
            const firstLine = lines[0].trim();
            if (firstLine.startsWith('{') && firstLine.endsWith('}')) {
                try {
                    JSON.parse(firstLine); // JSON
                    this.logger.debug('JSON');
                    return firstLine;
                } catch (e) {
                    this.logger.debug('JSON');
                }
            }
            
            // JSON
            let combinedJson = '';
            let openBraces = 0;
            let validJson = false;
            
            for (let i = 0; i < Math.min(lines.length, 5); i++) { // 5
                combinedJson += lines[i];
                openBraces += (lines[i].match(/{/g) || []).length;
                openBraces -= (lines[i].match(/}/g) || []).length;
                
                if (openBraces === 0 && combinedJson.trim().startsWith('{') && combinedJson.trim().endsWith('}')) {
                    try {
                        JSON.parse(combinedJson);
                        validJson = true;
                        this.logger.debug(`${i+1}JSON`);
                        break;
                    } catch (e) {
                        // 
                    }
                }
            }
            
            if (validJson) {
                return combinedJson;
            }
        }
        
        // JSONJSON
        const firstObjEnd = cleaned.indexOf('}{');
        if (firstObjEnd > 0) {
            this.logger.debug('JSON');
            cleaned = cleaned.substring(0, firstObjEnd + 1);
        }
        
        // 
        if (cleaned.includes('"done":') && !cleaned.includes('"response":')) {
            const matches = cleaned.match(/"content":"([^"]*)"/g);
            if (matches && matches.length > 0) {
                this.logger.debug('');
                let content = '';
                
                // 
                for (const match of matches) {
                    const contentMatch = match.match(/"content":"([^"]*)"/);
                    if (contentMatch && contentMatch[1]) {
                        content += contentMatch[1];
                    }
                }
                
                // response
                return `{"response": "${content.replace(/"/g, '\\"')}"}`;
            }
        }
        
        // 
        const openBracesCount = (cleaned.match(/{/g) || []).length;
        const closeBracesCount = (cleaned.match(/}/g) || []).length;
        
        if (openBracesCount > closeBracesCount) {
            // 
            this.logger.debug(` ${openBracesCount - closeBracesCount} `);
            cleaned = cleaned + '}}'.repeat(openBracesCount - closeBracesCount);
        } else if (closeBracesCount > openBracesCount) {
            // 
            this.logger.debug(` ${closeBracesCount - openBracesCount} `);
            const lastValidIndex = cleaned.length;
            for (let i = 0; i < closeBracesCount - openBracesCount; i++) {
                const lastBraceIndex = cleaned.lastIndexOf('}', lastValidIndex - 1);
                if (lastBraceIndex !== -1) {
                    cleaned = cleaned.substring(0, lastBraceIndex) + cleaned.substring(lastBraceIndex + 1);
                }
            }
        }
        
        // JSON
        if (!cleaned.startsWith('{')) {
            this.logger.debug('');
            const firstBrace = cleaned.indexOf('{');
            if (firstBrace >= 0) {
                cleaned = cleaned.substring(firstBrace);
            } else {
                cleaned = '{' + cleaned;
            }
        }
        
        if (!cleaned.endsWith('}')) {
            this.logger.debug('');
            const lastBrace = cleaned.lastIndexOf('}');
            if (lastBrace >= 0) {
                cleaned = cleaned.substring(0, lastBrace + 1);
            } else {
                cleaned = cleaned + '}';
            }
        }
        
        // JSON
        const jsonStart = cleaned.indexOf('{');
        const jsonEnd = cleaned.lastIndexOf('}');
        
        if (jsonStart >= 0 && jsonEnd > jsonStart) {
            this.logger.debug(`JSON: ${jsonStart}-${jsonEnd}`);
            cleaned = cleaned.substring(jsonStart, jsonEnd + 1);
        }
        
        // 
        cleaned = cleaned
            .replace(/[\u0000-\u001F]+/g, ' ')            // 
            .replace(/([^\\])"/g, '$1\\"')               // 
            .replace(/^([^{]*)({.*)$/, '$2')             // JSON
            .replace(/^{([^:]*):/, '{"response":');      // 
        
        // response
        if (!cleaned.includes('"response"')) {
            this.logger.debug('response');
            
            // 
            const textMatch = cleaned.match(/"([^"]{5,})"/);
            if (textMatch && textMatch[1]) {
                cleaned = `{"response": "${textMatch[1].replace(/"/g, '\\"')}"}`;
            } else {
                // 
                cleaned = '{"response": ""}';
            }
        }
        
        this.logger.debug(`JSON: ${cleaned.substring(0, Math.min(100, cleaned.length))}...`);
        return cleaned;
    }

    /**
     * 
     */
    private buildPrompt(context: any): string {
        // 
        const language = context.fileType || context.language || 'javascript';
        this.logger.debug(` ${language} `);
        
        let template = '';
        
        // 
        const hasChineseContent = context.textBeforeCursor && 
                                /[\u4e00-\u9fa5]/.test(context.textBeforeCursor);
        if (hasChineseContent) {
            this.logger.debug('');
        }
        
        // 
        if (context.commentMode || context.isInComment) {
            // 
            template += hasChineseContent 
                ? `${language}JSONobj['complete_code']\n\n`
                : `You are an expert ${language} programmer. Continue the following documentation comment based on the context. Only complete the comment, don't write any code. Do NOT return JSON format or structures like obj['complete_code']. ONLY output plain text comment. Focus ONLY on the comment at the cursor position, do not generate documentation for the entire file:\n\n`;
        } else {
            // 
            template += hasChineseContent
                ? `${language}${language}MarkdownJSONobj['complete_code']${language}\n\n`
                : `You are an expert ${language} programmer. Complete the ${language} code at the cursor position based on the context. Output ONLY valid ${language} code without any explanation, markdown formatting, or JSON structures. Do NOT wrap the code in obj['complete_code'] or any similar structure. Focus ONLY on completing the code at the cursor position - this could be a function, class, or a few lines of code, but do NOT regenerate the entire file. Output should be ONLY plain code that can be directly inserted at the cursor position:\n\n`;
        }
        
        // 
        if (context.documentText && context.documentText.length > 0) {
            // 
            const maxContextLength = 2000;
            const relevantContext = context.documentText.length > maxContextLength 
                ? context.documentText.substring(context.documentText.length - maxContextLength) 
                : context.documentText;
            
            template += `# Current file content (for context):\n${relevantContext}\n\n`;
        }
        
        // 
        if (context.textBeforeCursor) {
            template += `# Code before cursor:\n${context.textBeforeCursor}\n`;
        }
        
        // 
        if (context.previousCompletion) {
            this.logger.debug('');
            template += `\n# Previously completed part:\n${context.previousCompletion}\n`;
        }
        
        // 
        if (context.relevantCachedCode && context.relevantCachedCode.length > 0) {
            this.logger.debug('');
            template += `\n\n# Similar code for reference (don't repeat this):\n${context.relevantCachedCode}\n`;
        }
        
        // 
        template += `\n# Complete ONLY the code at cursor position (${language} code only):\n`;
        
        return template;
    }

    /**
     * 
     */
    private postProcessCompletion(completionText: string, context: any): string {
        if (!completionText) {
            return '';
        }
        
        const fileType = context.fileType || 'javascript';
        this.logger.debug(``);
        
        // JSON
        completionText = this.cleanJsonWrappedCode(completionText);
        
        // 
        if (fileType === 'python') {
            // PythonJavaScript
            const jsFeatures = /function\s+|var\s+|let\s+|const\s+|===|!==|this\.|prototype\.|=>|};/g;
            if (jsFeatures.test(completionText)) {
                this.logger.debug(`PythonJavaScript`);
                
                //  - 
                completionText = completionText
                    .replace(/function\s+([a-zA-Z0-9_]+)\s*\(/g, 'def $1(')  // functiondef
                    .replace(/var\s+|let\s+|const\s+/g, '')  // 
                    .replace(/this\./g, 'self.')  // thisself
                    .replace(/===|==/g, '==')  // 
                    .replace(/!==|!=/g, '!=')  // 
                    .replace(/;/g, '')  // 
                    .replace(/true/g, 'True')  // 
                    .replace(/false/g, 'False')
                    .replace(/null/g, 'None');
            }
        } else if (fileType === 'javascript' || fileType === 'typescript') {
            // JS/TSPython
            const pyFeatures = /def\s+|elif\s+|self\.|:\s*$/m;
            if (pyFeatures.test(completionText)) {
                this.logger.debug(`${fileType}Python`);
                
                // 
                completionText = completionText
                    .replace(/def\s+([a-zA-Z0-9_]+)\s*\(/g, 'function $1(')  // deffunction
                    .replace(/elif\s+/g, 'else if (')  // elifelse if
                    .replace(/self\./g, 'this.')  // selfthis
                    .replace(/True/g, 'true')  // 
                    .replace(/False/g, 'false')
                    .replace(/None/g, 'null');
                    
                // PythonJS()
                const lines = completionText.split('\n');
                const processedLines = [];
                for (let i = 0; i < lines.length; i++) {
                    let line = lines[i];
                    if (line.trim().endsWith(':')) {
                        line = line.replace(/:$/, ' {');
                    }
                    processedLines.push(line);
                }
                completionText = processedLines.join('\n');
            }
        }
        
        return completionText;
    }

    /**
     * JSON
     * obj['complete_code']
     */
    private cleanJsonWrappedCode(text: string): string {
        try {
            // JSON
            const trimmedText = text.trim();
            
            // JSON
            if (trimmedText.startsWith('{') && trimmedText.endsWith('}')) {
                try {
                    // JSON
                    const jsonObj = JSON.parse(trimmedText);
                    
                    // 
                    for (const key of ['code', 'complete_code', 'completion', 'content', 'result']) {
                        if (jsonObj[key] && typeof jsonObj[key] === 'string') {
                            this.logger.debug(`JSON: ${key}`);
                            return jsonObj[key];
                        }
                    }
                } catch (e) {
                    // JSON
                }
            }
            
            // JavaScript
            const objAccessPattern = /^\s*(?:let|const|var)?\s*(?:obj|result|response|output|completion)\s*(?:\[\s*['"](\w+)['"]\s*\]|\.(\w+))\s*(?:=\s*)?['"](.+)['"]\s*;?\s*$/s;
            const multilineObjPattern = /^\s*(?:let|const|var)?\s*(?:obj|result|response|output|completion)\s*(?:\[\s*['"](\w+)['"]\s*\]|\.(\w+))\s*(?:=\s*)?['"](.+)['"]$/s;
            
            let objMatch = text.match(objAccessPattern) || text.match(multilineObjPattern);
            if (objMatch && objMatch[3]) {
                this.logger.debug(``);
                // 
                return objMatch[3].replace(/\\n/g, '\n').replace(/\\"/g, '"').replace(/\\\\/g, '\\');
            }
            
            //  obj['complete_code'] = "..."
            const specificPatternRegex = /obj\s*\[\s*['"]complete_code['"]\s*\]\s*=?\s*['"]([\s\S]*?)['"]/;
            const specificMatch = text.match(specificPatternRegex);
            if (specificMatch && specificMatch[1]) {
                this.logger.debug(`obj['complete_code']`);
                return specificMatch[1].replace(/\\n/g, '\n').replace(/\\"/g, '"').replace(/\\\\/g, '\\');
            }
        } catch (error) {
            this.logger.debug(`JSON: ${error instanceof Error ? error.message : ''}`);
        }
        
        // 
        return text;
    }

    /**
     * 
     */
    private isInMultilineComment(text: string, language: string): boolean {
        if (['javascript', 'typescript', 'java', 'c', 'cpp', 'csharp'].includes(language)) {
            // 
            const openComments = (text.match(/\/\*/g) || []).length;
            const closeComments = (text.match(/\*\//g) || []).length;
            return openComments > closeComments;
        }
        
        if (language === 'python') {
            // 
            const openTripleQuotes = (text.match(/'''/g) || []).length;
            const openTripleDoubleQuotes = (text.match(/"""/g) || []).length;
            
            // 
            return (openTripleQuotes % 2 !== 0) || (openTripleDoubleQuotes % 2 !== 0);
        }
        
        return false;
    }

    /**
     * Ollama
     */
    public async testConnection(): Promise<{success: boolean, message: string, models?: string[]}> {
        this.logger.info(` Ollama : ${this.modelConfig.apiBase || this.configManager.getApiUrl()}`);
        
        try {
            const apiUrl = this.modelConfig.apiBase || this.configManager.getApiUrl();
            
            // 
            this.logger.debug(`: Node: ${process.version}, : ${process.platform}`);
            this.logger.debug(`: ${process.cwd()}`);
            this.logger.debug(`API URL: ${apiUrl}, : ${apiUrl}/api/tags`);
            
            this.logger.debug(`fetch ${apiUrl}/api/tags`);
            
            // 
            const response = await fetch(`${apiUrl}/api/tags`);
            
            this.logger.debug(`: =${response.status}, =${response.statusText}`);
            
            if (response.ok) {
                const responseText = await response.text();
                this.logger.debug(`: ${responseText}`);
                
                let data;
                try {
                    data = JSON.parse(responseText);
                } catch (jsonError) {
                    this.logger.error(`JSON: ${jsonError}`);
                    return {
                        success: false,
                        message: `JSON: ${responseText.substring(0, 100)}...`
                    };
                }
                
                if (data.models) {
                    const models = data.models.map((model: any) => model.name);
                    this.logger.info(` Ollama  ${models.length} : ${models.join(', ')}`);
                    return {
                        success: true,
                        message: 'Ollama',
                        models
                    };
                } else {
                    this.logger.warn(`models: ${JSON.stringify(data)}`);
                }
            } else {
                this.logger.warn(`Ollama API: ${response.status} ${response.statusText}`);
            }
            
            this.logger.warn(' Ollama ');
            return {
                success: true,
                message: 'Ollama',
                models: []
            };
        } catch (error) {
            this.logger.error(`Ollama: ${error instanceof Error ? error.message : String(error)}`);
            this.logger.error(`: ${error instanceof Error ? error.stack : ''}`);
            
            let errorMessage = 'Ollama';
            
            if (error instanceof Error) {
                const networkError = error as unknown as { code?: string };
                if (networkError.code === 'ECONNREFUSED') {
                    errorMessage = 'Ollama';
                } else if ('response' in error) {
                    const responseError = error as any;
                    errorMessage = `: ${responseError.response?.status} ${responseError.response?.statusText}`;
                }
            }
            
            return {
                success: false,
                message: errorMessage
            };
        }
    }

    /**
     * JSON
     */
    private extractCompletionDirectly(text: string): string | null {
        // JSON
        if (!text.includes('{') && !text.includes('}')) {
            return text.trim();
        }
        
        // JSON
        const parts = text.split('}');
        if (parts.length > 1) {
            // JSON
            const lastPart = parts[parts.length - 1].trim();
            if (lastPart.length > 0 && !lastPart.includes('{')) {
                return lastPart;
            }
        }
        
        // 
        const contentPatterns = [
            /"response"\s*:\s*"((?:\\"|[^"])*?)"/,  // response
            /"content"\s*:\s*"((?:\\"|[^"])*?)"/,   // content
            /"completion"\s*:\s*"((?:\\"|[^"])*?)"/ // completion
        ];
        
        for (const pattern of contentPatterns) {
            const match = text.match(pattern);
            if (match && match[1]) {
                return match[1].replace(/\\"/g, '"').replace(/\\n/g, '\n');
            }
        }
        
        return null;
    }
    
    /**
     * 
     */
    private extractAnyContent(text: string): string {
        // 1. JSON
        let content = text.replace(/[{}\[\]"]/g, ' ');
        
        // 2. 
        const colonIndex = content.indexOf(':');
        if (colonIndex > 0) {
            content = content.substring(colonIndex + 1);
        }
        
        // 3. 
        content = content
            .replace(/\\n/g, '\n')          // 
            .replace(/\s+/g, ' ')           // 
            .trim();                         // 
        
        // 4. 
        if (content.length < 5) {
            return '';
        }
        
        return content;
    }

    /**
     * 
     * @param prompt 
     * @param options 
     * @param signal 
     * @returns 
     */
    public async generateCompletion(
        prompt: string, 
        options: { temperature?: number; maxTokens?: number; model?: string }, 
        signal?: AbortSignal
    ): Promise<string | null> {
        try {
            const apiUrl = this.modelConfig.apiBase || this.configManager.getApiUrl();
            const modelName = options.model || this.modelConfig.model || this.configManager.getModelName();
            const temperature = options.temperature !== undefined ? options.temperature : this.modelConfig.temperature || this.configManager.getTemperature();
            const maxTokens = options.maxTokens || this.modelConfig.maxTokens || this.configManager.getMaxTokens();
            
            this.logger.debug(`: API URL=${apiUrl}, =${modelName}, =${temperature}, =${maxTokens}`);
            
            // 
            if (this.configManager.isDebugEnabled()) {
                this.logger.debug(`:\n${prompt}`);
            } else {
                // 100
                this.logger.debug(`100: ${prompt.substring(0, 100)}...`);
            }
            
            // 
            const requestData = {
                model: modelName,
                prompt: prompt,
                temperature: temperature,
                max_tokens: maxTokens,
                options: {
                    num_predict: maxTokens
                }
            };
            
            // 
            this.logger.debug(`Ollama API: ${apiUrl}/api/generate`);
            this.logger.debug(`: ${JSON.stringify(requestData).length} `);
            
            // 
            const fetchOptions: RequestInit = {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(requestData),
                signal: signal
            };
            
            // 
            this.logger.debug('fetch...');
            const response = await fetch(`${apiUrl}/api/generate`, fetchOptions);
            
            // 
            if (signal?.aborted) {
                this.logger.debug('');
                return null;
            }
            
            // 
            this.logger.debug(`Ollama API: ${response.status} ${response.statusText}`);
            
            // 
            if (!response.ok) {
                const errorText = await response.text();
                this.logger.error(`API: ${response.status} ${response.statusText} - ${errorText}`);
                throw new Error(`API: ${response.status} ${response.statusText} - ${errorText}`);
            }
            
            // 
            const responseText = await response.text();
            this.logger.debug(`: ${responseText.length}`);
            
            // JSON
            let completionText = '';
            
            // response
            if (responseText.includes('"response"')) {
                try {
                    this.logger.debug('responseJSON');
                    // 
                    const lines = responseText.split('\n').filter(line => line.trim() !== '');
                    this.logger.debug(`: ${lines.length}`);
                    
                    // response
                    let processedLines = 0;
                    for (const line of lines) {
                        try {
                            const jsonObj = JSON.parse(line);
                            if (jsonObj && jsonObj.response) {
                                completionText += jsonObj.response;
                                processedLines++;
                            }
                        } catch (parseError) {
                            this.logger.debug(`: ${parseError.message}`);
                        }
                    }
                    
                    this.logger.debug(`: ${processedLines}/${lines.length}`);
                    this.logger.debug(`: ${completionText.length}`);
                } catch (error) {
                    this.logger.error(`: ${error.message}`);
                    completionText = '';
                }
            } else {
                this.logger.debug('response');
            }
            
            // 
            if (!completionText || completionText.trim().length === 0) {
                this.logger.debug(``);
                try {
                    // response
                    let allResponses = '';
                    const regex = /"response":[ ]*"([^"]*)"/g;
                    let match;
                    let matchCount = 0;
                    
                    while ((match = regex.exec(responseText)) !== null) {
                        if (match[1]) {
                            // 
                            const responseValue = match[1].replace(/\\n/g, '\n').replace(/\\r/g, '\r').replace(/\\"/g, '"');
                            allResponses += responseValue;
                            matchCount++;
                        }
                    }
                    
                    if (allResponses.length > 0) {
                        this.logger.debug(`${matchCount}: ${allResponses.length}`);
                        completionText = allResponses;
                    } else {
                        this.logger.debug('');
                    }
                } catch (error) {
                    this.logger.debug(`: ${error.message}`);
                }
            }
            
            // 
            if (!completionText || completionText.trim().length === 0) {
                // 
                this.logger.debug('');
                const directExtract = this.extractCompletionDirectly(responseText);
                if (directExtract) {
                    completionText = directExtract;
                    this.logger.debug(`: ${completionText.length}`);
                } else {
                    this.logger.debug('');
                }
            }
            
            // Markdown
            if (completionText.startsWith('```')) {
                const firstLineBreak = completionText.indexOf('\n');
                if (firstLineBreak !== -1) {
                    // ```python```
                    completionText = completionText.substring(firstLineBreak + 1);
                } else {
                    completionText = '';
                }
                
                // ```
                const lastCodeBlockEnd = completionText.lastIndexOf('```');
                if (lastCodeBlockEnd !== -1) {
                    completionText = completionText.substring(0, lastCodeBlockEnd).trim();
                }
                this.logger.debug('Markdown');
            }
            
            // 
            if (completionText) {
                this.logger.debug(`: ${completionText.length}`);
                if (this.configManager.isDebugEnabled()) {
                    this.logger.debug(`200: ${completionText.substring(0, 200)}${completionText.length > 200 ? '...' : ''}`);
                }
            } else {
                this.logger.debug(``);
            }
            
            return completionText;
        } catch (error) {
            // 
            if (signal?.aborted) {
                this.logger.debug('');
                return null;
            }
            
            this.logger.error(`: ${error.message}`, error);
            throw error;
        }
    }
}