import * as fs from 'fs';
import * as path from 'path';
import * as vscode from 'vscode';
import { getFileName } from './utils';

/**
 * 
 * DEBUG < INFO < WARN < ERROR
 */
export enum LogLevel {
    NONE = 0,    // 
    DEBUG = 1,   // 
    INFO = 2,    // 
    WARN = 3,    // 
    ERROR = 4    // 
}

/**
 * 
 * 
 */
export class Logger {
    private static instance: Logger;
    private outputChannel: vscode.OutputChannel;
    private logLevel: LogLevel = LogLevel.NONE;
    private debugEnabled: boolean = false;

    private constructor() {
        this.outputChannel = vscode.window.createOutputChannel('TabAutoComplete');
    }

    public static getInstance(): Logger {
        if (!Logger.instance) {
            Logger.instance = new Logger();
        }
        return Logger.instance;
    }

    public setLogLevel(level: LogLevel): void {
        this.logLevel = level;
        this.log(LogLevel.INFO, `: ${LogLevel[level]}`);
    }

    private shouldLog(level: LogLevel): boolean {
        if (level === LogLevel.DEBUG && this.debugEnabled) {
            return true;
        }
        return this.logLevel !== LogLevel.NONE && level <= this.logLevel;
    }

    private formatMessage(level: LogLevel, message: string, data?: any): string {
        const timestamp = new Date().toISOString();
        const levelStr = LogLevel[level].padEnd(5);
        let formattedMessage = `[${timestamp}] [${levelStr}] ${message}`;
        
        if (data) {
            if (data instanceof Error) {
                formattedMessage += `\n    ${data.stack || data.message}`;
            } else if (typeof data === 'object') {
                try {
                    formattedMessage += `\n    ${JSON.stringify(data, null, 2)}`;
                } catch (e) {
                    formattedMessage += `\n    []`;
                }
            } else {
                formattedMessage += `\n    ${data}`;
            }
        }
        
        return formattedMessage;
    }

    private log(level: LogLevel, message: string, data?: any): void {
        if(level < this.logLevel){return;}
        // 
        const shouldLogMessage = this.shouldLog(level) || level === LogLevel.ERROR || level === LogLevel.WARN;
        
        if (shouldLogMessage) {
            const formattedMessage = this.formatMessage(level, message, data);
            this.outputChannel.appendLine(formattedMessage);
            
            // 
            if (level === LogLevel.ERROR) {
                console.error(formattedMessage);
            } else if (level === LogLevel.WARN) {
                console.warn(formattedMessage);
            }
        }
    }

    public debug(message: string, data?: any): void {
        this.log(LogLevel.DEBUG, message, data);
    }

    public info(message: string, data?: any): void {
        this.log(LogLevel.INFO, message, data);
    }

    public warn(message: string, data?: any): void {
        this.log(LogLevel.WARN, message, data);
        // 
        if (this.shouldLog(LogLevel.WARN)) {
            vscode.window.showWarningMessage(message);
            this.outputChannel.show(true); // 
        }
    }

    public error(message: string, error?: any): void {
        this.log(LogLevel.ERROR, message, error);
        // 
        vscode.window.showErrorMessage(message);
        this.outputChannel.show(true); // 
    }

    public setDebugEnabled(enabled: boolean): void {
        this.debugEnabled = enabled;
        this.log(LogLevel.INFO, `${enabled ? '' : ''}`);
    }

    public showOutputChannel(): void {
        this.outputChannel.show();
    }

    public dispose(): void {
        this.outputChannel.dispose();
    }
} 