/**
 * 
 * 
 * @param fn 
 * @param delay 
 */
export function debounce<T extends (...args: any[]) => Promise<any>>(fn: T, delay: number): (...args: any[]) => Promise<any> {
    let timer: NodeJS.Timeout | null = null;
    
    return function(...args: any[]): Promise<any> {
        return new Promise((resolve) => {
            if (timer) {
                clearTimeout(timer);
            }
            
            timer = setTimeout(async () => {
                const result = await fn(...args);
                resolve(result);
            }, delay);
        });
    };
}

/**
 * 
 * 
 * @param fn 
 * @param limit 
 */
export function throttle<T extends (...args: any[]) => any>(fn: T, limit: number): (...args: Parameters<T>) => ReturnType<T> | undefined {
    let lastCall = 0;
    let lastResult: ReturnType<T>;
    
    return function(this: any, ...args: Parameters<T>): ReturnType<T> | undefined {
        const now = Date.now();
        if (now - lastCall >= limit) {
            lastCall = now;
            lastResult = fn.apply(this, args);
            return lastResult;
        }
        return undefined;
    };
}

/**
 * 
 * 
 * @param code 
 * @param _language (_language)
 */
export function formatCode(code: string, _language: string): string {
    // 
    return code.trim();
}

/**
 * 
 * Levenshtein
 * @param str1 
 * @param str2 
 */
export function calculateLevenshteinDistance(str1: string, str2: string): number {
    const m = str1.length;
    const n = str2.length;
    
    // 
    const dist: number[][] = [];
    for (let i = 0; i <= m; i++) {
        dist[i] = [];
        dist[i][0] = i;
    }
    
    for (let j = 0; j <= n; j++) {
        dist[0][j] = j;
    }
    
    // 
    for (let i = 1; i <= m; i++) {
        for (let j = 1; j <= n; j++) {
            const cost = str1[i - 1] === str2[j - 1] ? 0 : 1;
            dist[i][j] = Math.min(
                dist[i - 1][j] + 1,      // 
                dist[i][j - 1] + 1,      // 
                dist[i - 1][j - 1] + cost // 
            );
        }
    }
    
    return dist[m][n];
}

/**
 * ID
 */
export function generateUniqueId(): string {
    return Date.now().toString(36) + Math.random().toString(36).substr(2, 5);
}

/**
 * JSON
 * @param text JSON
 * @param defaultValue 
 */
export function safeParseJSON<T>(text: string, defaultValue: T): T {
    try {
        return JSON.parse(text) as T;
    } catch (e) {
        return defaultValue;
    }
}

/**
 * 
 * @param filePath 
 */
export function getFileExtension(filePath: string): string {
    const match = filePath.match(/\.([^.]+)$/);
    return match ? match[1] : '';
}

/**
 * 
 * @param filePath 
 */
export function getFileName(filePath: string): string {
    const match = filePath.match(/([^/\\]+)$/);
    return match ? match[1] : '';
} 